import React, { FC } from "react";
import { graphql } from "gatsby";

interface IndexProps {
  data: any
}

type Props = IndexProps;

const Index: FC<Props> = ({ data }) => (
  <>
    <div className="bg-gray-light dark:bg-gray-dark py-32">
      <div className="container mx-auto">
        <h1 className="text-6xl font-bold">Developer / Software Engineer</h1>
        <h3 className="text-4xl font-semibold">Hey, I'm Joshua.</h3>
        <p className="text-3xl">I'm a Belfast-based developer, currently working at <a href="https://instil.co/" rel="nofollow">Instil</a>.</p>
        <p className="text-3xl">Previously worked with <a href="https://www.deloittedigital.co.uk/" rel="nofollow">Deloitte Digital</a> in the Gasworks &amp; <a href="https://www.surfsidesolutions.com/" rel="nofollow">Surfside Solutions</a> down in Annalong.</p>
        <div className="text-2xl pt-6">
          <a className="pr-6" href="https://www.github.com/joshwjb" rel="nofollow">GitHub</a>
          <a className="pr-6" href="https://www.twitter.com/joshwjb" rel="nofollow">Twitter</a>
          <a href="https://uk.linkedin.com/in/joshu" rel="nofollow">LinkedIn</a>
        </div>
      </div>
    </div>
    {/* <div className="py-32 bg-white dark:bg-black">
      <div className="container mx-auto">
        <h2 className="text-5xl font-semibold">Blog Posts</h2>
        Hi
      </div>
    </div> */}
  </>
);

export default Index;

// Based on https://blog.logrocket.com/creating-a-gatsby-blog-from-scratch/
export const pageQuery = graphql`
  {
    blog: allMarkdownRemark {
      posts: nodes {
        frontmatter {
          date(fromNow: true)
          title
          author
        }
        excerpt
        id
      }
    }
  }
`;
